:root {
  --font-main: "Segoe UI", Arial, sans-serif;
  --font-size-large: 1.9rem;
  --font-size-h1: 2.6rem;
  --font-size-table: 1.9rem;
  --color-bg: #23272f;
  --color-bg-table: #23272ffa;
  --color-header: #5fa8e9;
  --color-header-row: #263a53;
  --color-header-text: #b3d8ff;
  --color-row-alt: #7882962e;
  --color-row-hover: #5078c821;
  --color-text: #f5f5f5;
  --color-shadow: #0004;
  --color-btn: #22c55e;
  --color-btn-hover: #16a34a;
  --padding-table-cell: 12px 20px;
}

html {
  font-size: 16px;
}

body {
  font-family: var(--font-main);
  background: var(--color-bg);
  color: var(--color-text);
  letter-spacing: .01em;
  height: 100vh;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  line-height: 1.4;
  overflow-y: hidden;
}

button[aria-label="Add Game"] {
  background: var(--color-btn);
  color: #fff;
  width: 64px;
  height: 64px;
  box-shadow: 0 4px 16px var(--color-shadow);
  cursor: pointer;
  z-index: 1000;
  border: none;
  border-radius: 50%;
  font-size: 2.2rem;
  transition: background .2s;
  position: fixed;
  bottom: 32px;
  right: 32px;
}

button[aria-label="Add Game"]:hover {
  background: var(--color-btn-hover);
}

.modal-overlay {
  z-index: 2000;
  background: #000a;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background: var(--color-bg-table);
  color: #fff;
  border-radius: 16px;
  min-width: 320px;
  max-width: 90vw;
  padding: 48px;
  box-shadow: 0 8px 32px #0008;
}

.scoreboard-edit-btn, .scoreboard-save-btn, .scoreboard-placement-btn, .scoreboard-placement-btns-container, .scoreboard-placement-flex-row {
  display: none !important;
}

h1 {
  text-align: center;
  letter-spacing: 2.5px;
  color: var(--color-header);
  font-size: var(--font-size-h1);
  text-shadow: 0 4px 24px #000a, 0 1px #3762c2;
  margin-top: 18px;
  margin-bottom: 12px;
  font-weight: 800;
}

h2 {
  text-align: center;
  color: var(--color-header-text);
  letter-spacing: 1.2px;
  text-shadow: 0 2px 8px #0006;
  margin-top: 10px;
  margin-bottom: 6px;
  font-weight: 500;
}

.app-container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 24px 0 0;
}

.app-header-row {
  align-items: center;
  gap: .5em;
  margin-bottom: 1.5em;
  display: flex;
}

.app-header-row h1 {
  color: #5fa8e9;
  margin: 0;
  font-size: 2.1rem;
  font-weight: 700;
  line-height: 1.1;
}

.scoresheet-select {
  background: var(--color-header-row);
  color: var(--color-header-text);
  border: none;
  border-radius: 6px;
  outline: none;
  min-width: 100px;
  padding: 4px 12px;
  font-size: 1.2rem;
}

#scoresheet-select {
  color: #5fa8e9;
  cursor: pointer;
  box-shadow: none;
  appearance: none;
  vertical-align: middle;
  background: none;
  border: none;
  border-radius: 6px;
  outline: none;
  height: 1.3em;
  margin-left: .3em;
  padding: .05em .5em .05em .3em;
  font-family: inherit;
  font-size: 2.1rem;
  font-weight: 700;
  line-height: 1.1;
  transition: background .18s;
  display: inline-block;
}

#scoresheet-select:hover {
  color: #5fa8e9;
  box-shadow: none;
  background: #b3d8ff;
  border: none;
}

#scoresheet-select:focus {
  color: #5fa8e9;
  box-shadow: none;
  background: none;
  border: none;
}

#scoresheet-select option {
  color: #23293a;
  background: #eaf6ff;
  font-weight: 600;
}

.modal-close-btn {
  background: var(--color-btn);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  margin-top: 16px;
  padding: 8px 18px;
  font-size: 1.1rem;
  font-weight: 600;
  transition: background .2s;
}

.modal-close-btn:hover {
  background: var(--color-btn-hover);
}

.scryfall-popup {
  z-index: 4000;
  color: #eaf6ff;
  background: #23293a;
  border: 1.5px solid #5fa8e9;
  border-radius: 10px;
  outline: none;
  min-width: 220px;
  max-width: 340px;
  max-height: 240px;
  padding: .15rem 0;
  font-family: inherit;
  font-size: 1.08rem;
  animation: .18s cubic-bezier(.4, 1.6, .6, 1) fadeInScryfall;
  position: fixed;
  overflow-y: auto;
  box-shadow: 0 8px 32px #000b;
}

@keyframes fadeInScryfall {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.scryfall-popup-item {
  cursor: pointer;
  text-align: left;
  width: 100%;
  color: inherit;
  background: none;
  border: none;
  border-radius: 0;
  outline: none;
  padding: 9px 18px;
  font-family: inherit;
  font-size: 1.08rem;
  transition: background .13s, color .13s;
}

.scryfall-popup-item:hover, .scryfall-popup-item.active, .scryfall-popup-item:focus {
  color: #5fa8e9;
  background: #263a53;
  outline: none;
}
